import React, {useState} from 'react'
import LoadingSpinner from './LoadingSpinner'
import ItemInList from './ItemInList'

export default function ListView({xc}) {

    console.log('render ListView')

    const format = (map) => {
        // Make list of tasks from map of tasks
        let list = []
        map.forEach((value, key)=>{
            const task_with_key = value
            task_with_key.key = key
            list.push(
                task_with_key
            )
        })

        // order list of tasks
        list.sort((a,b)=>a.dueDate - b.dueDate)

        // format into jsx
        let formatted_list = []
        list.forEach((task)=> {
            formatted_list.push(
                <ItemInList t={task} key={task.key}/>
            )
        })
        return (
            <article className='d-flex pt-0 px-3 flex-column'>
                {formatted_list}
            </article>
        )
    }

    const [content, setContent] = useState()
    if (!content) {
        xc.get_all_tasks()
        .then((results) => {
            setContent(format(results))
        })
    }
    
    
    return (
        <>
            {content? content : <LoadingSpinner/>}
        </>
    )
}