import React from 'react'

export default function ItemInList({t}) {
    // console.log('render item in list')
    // console.log(t)
    return (
        <div className='d-flex pt-3'>
            <div className='fs-5 me-auto'>
                {t.taskName}
            </div>
            <div>
                {t.dueDate.toDate().toLocaleString()}
            </div>
        </div>
    )
}